import image from '../../resources/images/MxNet Layout.PNG'
const MainLayout = () => {
    return (
        <div className = 'image-container'>
            <svg viewBox="0 0 1800 750">
                <image href={image} width="100%"  />
                
                <a id="tail-number" href=""><rect x="25" y="542" width="70" height="35" /></a>
                <a id="barcode" href=""><rect x="120" y="542" width="110" height="35" /></a>
                <a id="type" href=""><rect x="255" y="542" width="70" height="35" /></a>
                <a id="location" href=""><rect x="345" y="542" width="70" height="35" /></a>
                <a id="maint-provider" href=""><rect x="500" y="542" width="130" height="35" /></a>
                <a id="fleet" href=""><rect x="970" y="542" width="90" height="35" /></a>
                <a id="mer-recieved" href=""><rect x="1200" y="542" width="90" height="35" /></a>
                <a id="comment" href="">< rect x="1385" y="542" width="90" height="35" /></a>
                <a id="date-range" href=""><rect x="445" y="110" width="910" height="50" /></a>
                <a id="search-statuses" href=""><rect x="445" y="172" width="910" height="70" /></a>
                <a id="other-options" href=""><rect x="445" y="255" width="910" height="180" /></a>
            
            </svg>  
        </div>
    )
}

export default MainLayout


