
import './MxNetLayout.css'
import { useState, useEffect } from 'react'
// import InfoPanel from '../InfoPanel'
import infoOptions from './mxInfoPanel'
import MainLayout from './MainLayout'
import RtsDetailsLayout from './RtsDetailsLayout'


const MxNetLayout = ({updateOptions}) => {
    const [activeLayout, setActiveLayout] = useState('MainLayout')


    const updateActiveLayout = (layout) => {
        setActiveLayout(() => layout)
    }

    const onLayoutButtonClick = (e) => {
        console.log('e.target.value: ', e.target.value)
        setActiveLayout(e.target.value)

    }

    return (
        <>

        
        {/* return the options to app to update the state */}
        {updateOptions(infoOptions)}
        
        
        <div className='mxnet-layout-container'>
            <div className='layout-options-container'>
                <button value={'MainLayout'} onClick={e => onLayoutButtonClick(e)}>Search Page</button>
                <button value={'RTS Details Page'} onClick={e => onLayoutButtonClick(e)}>RTS Details Page</button>
                   
            </div>
            {activeLayout === 'MainLayout' ? <MainLayout /> : <RtsDetailsLayout />}
            {/* <MainLayout /> */}
            {/* <RtsDetailsLayout /> */}


        </div>

        </>
    )
}

export default MxNetLayout
