const Povdata = () => {


    return (
        <div className='pov-content-container'>
            <h1 className='pov-header'>
                {headers[0]}
            </h1>

            <div className='section'>

            
                <h2>
                    {subHeaders[0]}
                </h2>

                <ol>
                    <li>
                        {data[subHeaders[0]][0]}
                    </li>
                    <li>
                        {data[subHeaders[0]][1]}
                    </li>
                    
                </ol>

            </div>

            <div className='section'>
                <h2>
                    {subHeaders[1]}
                </h2>

                <ol>
                    <li>
                        {data[subHeaders[1]][0]}
                    </li>
                    <li>
                        {data[subHeaders[1]][1]}
                    </li>
                    
                </ol>
            </div>

            <div className='section'>

                <h2>
                    {subHeaders[2]}
                </h2>

                <ol>
                    <li>
                        {data[subHeaders[2]][0]}
                    </li>
                    <li>
                        {data[subHeaders[2]][1]}
                    </li>
                    <li>
                        {data[subHeaders[2]][2]}
                    </li>
                    
                </ol>
            </div>


        </div>
    )
}

export default Povdata
