

const Navbar = ( {className, prefix, tabNames, onNavClick}) => {
    console.log('navbar re-rerender')

    const handleClick= (e) => {
        
        onNavClick(e)
        
        //e.preventDefault()
    }

    return (
        <nav className={className}>

            <ul>
                {tabNames.map((tab, i) => (
                    <li key={i}>
                        {prefix ? 

                        <a value={tab} onClick={e => handleClick(e)} href={`${prefix}/${tab}`}>{tab}</a> :
                        <a value={tab} onClick={e => handleClick(e)} href={`/${tab}`}>{tab}</a> }
                         
                        
                    </li>
                ))}

            </ul>
            
        </nav>
    )
}

export default Navbar
