

var data = {
    MxNet: [
        "Database containing every Work Order",
        "Provides search filters such as Date, Tail Number, or Work Order Barcode",
        "Generally used to find the oldest available work orders that require data entry",
    ],

    OnBase: [
        "Application and Database containing Work Orders and scanned copies of\
          each document within that Work Order",
        "The Work Orders reside within different queues corresponding to their\
         stage in our process", 
         "Generally used to ensure we have every document associated with each\
          Work Order and that they are labeled (indexed) correctly",

    ],

    Maintinex: [
        "Database that contains each Work Order and their associated tasks that were\
         completed during maintenance",
        "Data from a Work Order’s documents in OnBase is entered here",
        
    ],
 
    

} 


var headers = ['Application Overview']
var subHeaders = ['MxNet', 'OnBase', 'Maintinex']


const Aovdata = () => {


    return (
        <div className='pov-content-container'>
            <h1 className='pov-header'>
                {headers[0]}
            </h1>

            <div className='section'>

            
                <h2>
                    {subHeaders[0]}
                </h2>

                <ol>
                    <li>
                        {data[subHeaders[0]][0]}
                    </li>
                    <li>
                        {data[subHeaders[0]][1]}
                    </li>
                    <li>
                        {data[subHeaders[0]][2]}
                    </li>
                    
                </ol>

            </div>

            <div className='section'>
                <h2>
                    {subHeaders[1]}
                </h2>

                <ol>
                    <li>
                        {data[subHeaders[1]][0]}
                    </li>
                    <li>
                        {data[subHeaders[1]][1]}
                    </li>
                    <li>
                        {data[subHeaders[1]][2]}
                    </li>
                    
                </ol>
            </div>

            <div className='section'>

                <h2>
                    {subHeaders[2]}
                </h2>

                <ol>
                    <li>
                        {data[subHeaders[2]][0]}
                    </li>
                    <li>
                        {data[subHeaders[2]][1]}
                    </li>
                    
                    
                </ol>
            </div>


        </div>
    )
}

export default Aovdata
